//   ///

// GNB  
import { Link, useNavigate } from "react-router-dom";
import { menu } from "../data/gnb";

//  CSS 
import "../../css/top_area.scss";
import Logo from "../modules/Logo";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";

// 
import $ from "jquery";

export default function TopArea() {
  //  ////
  const goNav = useNavigate();
  //  goNav(,{})
  //   !
  // :  useNavigate()  
  //     
  // ) goNav('/news') -> 
  // ) goNav('news') -> 
  // ) goNav('/') -> 
  // ) goNav('') -> 
  //   !
  //     
  // ->   !

  //    ///////////
  // 1.  
  const showSearch = (e)=>{
    // 
    e.preventDefault();
    // 1.  
    $(".searchingGnb").show();
    // show() - display 
    // 2.   
    $("#schinGnb").focus();
  }; ////// showSearch  ///////

  // 2.     
  const enterKey = e => {
    // e.keyCode , e.key 
    // console.log(e.key,e.keyCode);
    if(e.key == "Enter"){
      //    : val()
      let txt = $(e.target).val().trim();
      console.log(txt);
      //    (!)
      if(txt!=''){
        //    
        $(e.target).val("").parent().hide();
        //  
        goSearch(txt);

      } /// if ///
    } //// if ////

  }; ///////// enterKey //////////

  // 3.    
  const goSearch = txt => {
    console.log("  ~!!!");
    //   
    // (,{state:{}})
    goNav("search",{state:{keyword:txt}});
  }; /////////// goSearch //////////////

  ////   //////////////
  return (
    <>
      {/* 1. */}
      <header className="top-area">
        {/*    */}

        {/*  GNB */}
        <nav className="gnb">
          <ul>
            {/* 1.   */}
            <li>
              <a
                href="#"
                onClick={(e) => {
                  // 
                  e.preventDefault();
                  //   
                  goNav("");
                }}
              >
                <Logo logoStyle="top" />
              </a>
              {/* <Link to="/">
                <Logo logoStyle="top" />
              </Link> */}
            </li>
            {/* 2. GNB    */}
            {menu.map((v, i) => (
              <li key={i}>
                {
                  //     a 
                  //  Link  
                  v.sub ? (
                    <a href="#">{v.txt}</a>
                  ) : (
                    <Link to={v.link}>{v.txt}</Link>
                  )
                }
                {
                  //     
                  v.sub && (
                    <div className="smenu">
                      <ol>
                        {v.sub.map((v, i) => (
                          <li key={i}>
                            <Link to={v.link}>{v.txt}</Link>
                          </li>
                        ))}
                      </ol>
                    </div>
                  )
                }
              </li>
            ))}
            {/* 3. , ,  */}
            <li
              style={{
                marginLeft: "auto",
                marginRight: "25px",
              }}
            >
              {/*  */}
              <div className="searchingGnb">
                {/*    */}
                <FontAwesomeIcon
                  icon={faSearch}
                  className="schbtnGnb"
                  title="Open search"
                />
                {/*  */}
                <input
                  type="text"
                  name="schinGnb"
                  id="schinGnb"
                  placeholder="Filter by Keyword"
                  onKeyUp={enterKey}
                />
              </div>
              {/*  -   */}
              <a href="#" onClick={showSearch}>
                <FontAwesomeIcon icon={faSearch} />
              </a>
            </li>
          </ul>
        </nav>
      </header>
    </>
  );
} /////////// TopArea /////////////////////
