import React, { useEffect } from "react";

//   state  
import { useLocation } from "react-router-dom";

import Banner from "../modules/Banner";
import CatList from "../modules/CatList";

// CSS
import "../../css/cat_detail.scss";

function CatDetail() {
  //     !
  const loc = useLocation();
  const cname = loc.state.cname;
  const cdesc = loc.state.cdesc;
  const facts = loc.state.facts;
  // console.log(cname, cdesc, facts);

  //   ////
  //    
  //    !
  useEffect(()=>{
    window.scrollTo(0,0);
  });

  //  //////////////////
  return (
    <>
      {/* 1. */}
      <Banner catName={cname} />
      {/* 2. */}
      <div className="detail">
        {/* 2-1.  */}
        <div className="desc-box">
          {/*  */}
          <h2>{cname}</h2>
          {/*  */}
          <div className="cdesc">
            {
              //   "^" 
              //    p
              //  ! -> split()
              cdesc.split("^").map((v, i) => (
                <p key={i}>{v}</p>
              ))
              // console.log(cdesc.split("^"))
            }
          </div>
        </div>
        {/* 2-2. */}
        <div className="facts">
          <div>
            <h3>CHARACTER FACTS</h3>
            {/*   tr ! */}
            <table>
              <tbody>
                {facts.split("^").map((v, i) => (
                  <tr key={i}>
                    {v.split(":").map((v, i) => (
                      <td key={i}>
                        {v}
                        {i == 0 && " : "}
                      </td>
                    ))}
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
      {/* 3. */}
      <CatList />
    </>
  );
}

export default CatDetail;
