//    

import React, { useEffect } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
//  
import $ from "jquery";

// Import Swiper styles :   CSS !
import "swiper/css";
import "swiper/css/navigation";

//  
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCirclePlay } from "@fortawesome/free-regular-svg-icons";
// import { faHand } from "@fortawesome/free-solid-svg-icons";
// import { faGooglePlay } from "@fortawesome/free-brands-svg-icons";

//    CSS :   CSS
import "./css/swiper_vid.scss";

//  
import { swVidData } from "../data/swiper_vid";

// import required modules
//    
// (  - )
import { Navigation } from "swiper/modules";

export function SwiperVid({ catName }) {
  // catName - 

  //  
  const selData = swVidData[catName];

  //    /////
  const showVideo = (src, tit) => {
    console.log("!", src, tit);

    // 1. 
    // 1-1.  : .play-vid iframe
    const ifr = $(".play-vid iframe");
    // 1-2.   : .vid-bx
    const vbx = $(".vid-bx");
    // 1-3.   : .ifr-tit
    const itit = $(".ifr-tit");
    // 1-4.   : .cbtn
    const cbtn = $(".cbtn");

    // 2. 
    // 2-1.  src 
    ifr.attr("src", src + "?autoplay=1");
    // 2-2.   
    itit.text(tit);
    // 2-3. 
    vbx.fadeIn(300);
    // 2-4.  
    cbtn.on("click", () => {
      //  
      vbx.fadeOut(300);
      //    (src)
      ifr.attr("src", "");
    }); //// click /////
  }; /////////// showVideo ////////

  return (
    <>
      <Swiper
        // slidesPerView={4}
        spaceBetween={20}
        navigation={true}
        /*     */
        modules={[Navigation]}
        //    !
        breakpoints={{
          200: {
              slidesPerView: 1,
          },
          500: {
              slidesPerView: 2,
          },
          1000: {
              slidesPerView: 3,
          },
          1200: {
              slidesPerView: 4,
          },
        }}
        className="mySwiper"
      >
        {selData.map((v, i) => (
          <SwiperSlide key={i}>
            <section
              className="sw-inbox"
              onClick={() => showVideo(v.vsrc, v.tit)}
            >
              {/*  */}
              <div className="vid-img">
                <img 
                src={process.env.PUBLIC_URL+v.isrc} 
                alt={v.tit} />
                {/*   */}
                <FontAwesomeIcon
                  icon={faCirclePlay}
                  style={{
                    position: "absolute",
                    bottom: "55%",
                    left: "10%",
                    color: "#fff",
                    fontSize: "50px",
                  }}
                />
              </div>
              {/*    */}
              <div className="vid-tit">
                <h4>{v.cat}</h4>
                <h3>{v.tit}</h3>
              </div>
            </section>
          </SwiperSlide>
        ))}
      </Swiper>
    </>
  );
} /////////// SwiperVid  ///////////
